.modal--log-in {
	.modal__content {
		width: 100%;
		max-width: 580px;
		min-height: 640px;
		padding: 0;
		background: #f5f1ee;
		box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
	}
}

.log-in {
	border-top-left-radius: inherit;
	border-top-right-radius: inherit;
	&__header {
		min-height: 100px;
		background-color: $color-fantasy;
		border-top-left-radius: inherit;
		border-top-right-radius: inherit;
		padding: 40px 40px 20px;
		@include vp--1023 {
			padding: 40px 20px 20px;
		}
	}
	&__tabs {
		&-nav {
			height: 40px;
			border-bottom: 1px solid rgba($color-islamic-green, 0.4);
			@include vp--768 {
				overflow-x: auto;
				overflow-y: hidden;
			}
		}
		&-navbar {
			width: 500px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			gap: 15px;
		}
		&-switch {
			position: relative;
			font-style: normal;
			font-weight: normal;
			font-size: 18px;
			line-height: 22px;
			color: $color-semi-sweet-chocolate;
			transition: all 0.3s ease 0s;
			&::after {
				content: "";
				display: inline-block;
				width: 100%;
				height: 5px;
				background-color: $color-islamic-green;
				opacity: 0;
				visibility: hidden;
				transition: all 0.3s ease 0s;
			}
			&._active {
				font-weight: 500;
				&::after {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}
	&__wrapper {
		padding: 20px 40px 40px 40px;
		@include vp--1023 {
			padding: 20px;
		}
	}
	&__tabs-block {
		display: none;
		&._active {
			display: block;
		}
	}
	.custom-input {
		&__label {
			display: none;
		}
		input {
			border: none;
			border-bottom: 1px solid rgba($color-semi-sweet-chocolate, 0.4);
			border-radius: unset;
			padding: 5px;
			background-color: inherit;
			&::placeholder {
				font-size: 14px;
				line-height: 1.43;
				opacity: 1;
			}
		}
		&.is-invalid {
			input {
				&::placeholder {
					color: $color-alizarin;
				}
			}
		}
	}

	&__form {
		.custom-select {
			padding-left: 0;
			margin-top: 0;
			&__button {
				padding: 5px 0 5px 5px;
				border-radius: unset;
				border: none;
				border-bottom: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
			}
			&__placeholder {
				left: 5px;
			}
			&.is-invalid {
				.custom-select__placeholder {
					color: $color-alizarin;
				}
			}
			&__list {
				width: 100% !important;
			}
		}
		&-error {
			margin: 35px 0 0 0;
			color: $color-alizarin;
			font-weight: normal;
			font-size: 12px;
			line-height: 20px;
			opacity: 0;
			visibility: hidden;
			transition: all 0.3s ease 0s;
			&._active {
				opacity: 1;
				visibility: visible;
			}
		}
	}
	&__link {
		display: block;
		font-style: normal;
		font-weight: normal;
		font-size: 14px;
		line-height: 20px;
		text-decoration-line: underline;
		color: $color-semi-sweet-chocolate;
		padding: 5px 0;
		&:hover {
			text-decoration: none;
		}
	}
	&__row {
		display: flex;
		justify-content: space-between;
		margin: 20px 0 0 0;
		@include vp--768 {
			flex-wrap: wrap-reverse;
		}
	}
	&__checkbox label,
	&__obligatory {
		font-weight: normal;
		font-size: 12px;
		line-height: 18px;
	}
	&__checkbox:not(:last-child) {
		margin-bottom: 0 !important;
	}
	&__checkbox {
		@include vp--768 {
			margin-top: 15px;
		}
	}
	&__obligatory {
		flex: 0 0 155px;
		@include vp--768 {
			margin: 0;
		}
		&.is-invalid {
			.log-in__obligatory-span {
				color: $color-alizarin;
			}
		}
		&.account__notification {
			font-weight: normal;
			font-size: 12px;
			line-height: 18px;
			& span {
				padding-top: 0;
			}
		}
	}
	.forgot {
		&__title,
		&__text {
			font-style: normal;
			font-weight: 600;
			font-size: 14px;
			line-height: 20px;
			color: $color-semi-sweet-chocolate;
			margin: 0 0 20px;
		}
		&__text {
			font-weight: normal;
			margin: 0;
		}
		&__form {
			form {
				margin: 40px 0 0 0;
			}
		}
	}
	&__btn {
		margin: 40px 0;
		min-width: unset;
		max-width: 280px;
		width: 100%;
		@include vp--425 {
			max-width: 100%;
		}
	}
}
